import React from 'react'
import { Maze } from 'z-ui-kit'
import './dfs.css'
export default class Dfs extends React.Component {
  constructor (props) {
    super(props)
    this.maze = null
    this.state = {
      started: false
    }
    this.start = this.start.bind(this)
  }
  componentDidMount () {
    console.log('here')
    this.maze = new Maze('#dfs-demo', (status) => this.setState({ started: false }))
  }
  start () {
    this.maze.init()
    this.setState({ started: true })
  }
  render () {
    return (
      <div>
        <div className='demo-wrapper'>
          <canvas id='dfs-demo' width='588' height='260' style={{ backgroundColor: 'red' }} />
          <p><button className='demo-btn' onClick={() => this.start()} disabled={this.state.started}>Start</button></p>
        </div>
      </div>
    )
  }
}


